	module.exports = {"data":{"post":{"html":"<p>Jeyser is an Open-source software, and we are open to contributions. However, here are some minimal rules.</p>\n<p><strong>Don't do it alone</strong> </p>\n<p>If you create your own module without asking before, there are large chances your pull-request will be refused. open an issue on github to discuss about it before.</p>\n<p><strong>Be general</strong></p>\n<p>Only general modules will be integrated into the core of Jeyser CRM. A module is general if it can be used by every Junior-Entreprise. Every means every, a module helping juste a subset of JE even though it's a large one won't probably be merged.</p>\n<p>If your module is not general enough, you can still develop it and plug it into your installation. We would be glad to help you on that subject. </p>\n<p>On the contrary, if you have a bright idea but can't develop it, come on <a href=\"https://github.com/n7consulting/Incipio\" target=\"_blank\" rel=\"nofollow noopener noreferrer\">Github</a> and chat with us, we can certainly do something for you.</p>\n<p><strong>Develop module only if the use time on the next 2 years will be larger than your development time</strong></p>\n<p>Once you will have the power on your Information System, it's very likely everyone will want its own Jeyser module. We also had that at N7 Consulting. We defined a simple rule to validate module development : the development time should be smaller than the expected use time on the next 2 years. We think apply it for external contributions will help Jeyser.</p>\n<p><strong>Test it</strong></p>\n<p>Jeyser is not well covert by behavioural tests. We are working on it. We won't accept external contributions without 75 % code coverage of their code.</p>\n<p><strong>Not Ok with these rules ?</strong> </p>\n<p>It's up to you. If you disagree with these rules, just fork the project and work on your own. However, we don't think it's the good solution, because fragmenting the community won't make it better. And we won't provide support for your work.</p>"},"navDoc":{"edges":[{"node":{"title":"Installation","path":"install","items":[{"id":"environment","title":"Environment","anchors":null},{"id":"index","title":"Install Jeyser in minutes","anchors":null},{"id":"update","title":"Update Jeyser","anchors":null},{"id":"annexes","title":"Annexes","anchors":[{"id":"help-for-manual-installation","title":"Help for manual installation"},{"id":"ssh-key-connection","title":"SSH Key Connection"}]}]}},{"node":{"title":"Features","path":"features-fr","items":[{"id":"gestion-associative","title":"Gestion associative","anchors":null},{"id":"suivi-projet","title":"Suivi de projet","anchors":null},{"id":"prospection","title":"Prospection","anchors":null},{"id":"rh","title":"Relations humaines","anchors":null},{"id":"tresorerie","title":"Trésorerie","anchors":null},{"id":"formations","title":"Formations","anchors":null},{"id":"indicateurs","title":"Indicateurs","anchors":null},{"id":"documents-types","title":"Documents types","anchors":null},{"id":"documents-types-faq","title":"FAQ Documents types","anchors":null},{"id":"administration","title":"Administration","anchors":null},{"id":"gdpr","title":"Règlement général sur la protection des données","anchors":null}]}},{"node":{"title":"User guides","path":"user-guides","items":[{"id":"introduction-to-jeyser-1","title":"Introduction to Jeyser","anchors":null}]}},{"node":{"title":"Development","path":"dev","items":[{"id":"faq","title":"FAQ","anchors":null},{"id":"docker-architecture","title":"Architecture Docker","anchors":null},{"id":"about","title":"About","anchors":null},{"id":"demo-data","title":"Demo data","anchors":null},{"id":"contributing","title":"Contributing","anchors":null}]}}]}},"pathContext":{"path":"docs/dev/contributing","current":{"path":"docs/dev/contributing","title":"Development - Contributing"},"prev":{"path":"docs/dev/demo-data","title":"Demo data","rootPath":"Development"},"next":false}}

